body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

.seccion {
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
}
.seccion .titulo {
  text-align: center;
  padding: 1rem;
}
.seccion .titulo h2 {
  margin: 0;
  padding-bottom: 0.5rem;
  text-align: center;
  color: #ffffff;
}
.seccion .titulo hr {
  width: 25%;
  height: 2px;
  border: none;
  background: #00a6fb;
}
.seccion .titulo p {
  color: #ffffff;
}

.contenedor {
  max-width: 1200px;
  margin: 0 auto;
}

.txt-azul {
  color: #2965f1;
}

.txt-amarillo {
  color: #e08709;
}

.txt-anaranjado {
  color: #ee6c4d;
}

.header {
  margin: 0;
  padding-bottom: 2.5rem;
  background-image: linear-gradient(to right, rgba(0, 53, 84, 0.5), rgba(0, 53, 84, 0.7)), url("../img/portada.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
  background-attachment: fixed;
}
.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  overflow: hidden;
}
.header .nav .logo {
  width: 25%;
}
@media (min-width: 480px) {
  .header .nav .logo {
    width: 10%;
  }
}
.header .nav .contenedorMenu {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 480px) {
  .header .nav .contenedorMenu {
    width: 70%;
  }
}
.header .nav .contenedorMenu .btnMovil {
  color: #ffffff;
  font-size: 3.5rem;
}
@media (min-width: 480px) {
  .header .nav .contenedorMenu .btnMovil {
    display: none;
  }
}
.header .nav .contenedorMenu .menu {
  visibility: hidden;
  position: absolute;
  top: 20vh;
  left: 0;
}
@media (min-width: 480px) {
  .header .nav .contenedorMenu .menu {
    position: relative;
    visibility: visible;
    top: 0;
  }
}
.header .nav .contenedorMenu .menu ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 480px) {
  .header .nav .contenedorMenu .menu ul {
    opacity: 1;
    flex-direction: row;
  }
}
.header .nav .contenedorMenu .menu ul .item {
  display: inline-block;
  padding: 1rem;
  color: #ffffff;
  font-weight: 700;
  border-bottom: 2px solid transparent;
  font-size: 2rem;
}
.header .nav .contenedorMenu .menu ul .item:hover {
  color: rgb(0, 166, 251);
  border-bottom: 2px solid rgb(0, 166, 251);
}
.header .contenido {
  padding: 1rem;
  max-width: 90%;
  color: #ffffff;
}
@media (min-width: 480px) {
  .header .contenido {
    font-size: 2rem;
    max-width: 50%;
  }
}
.header .contenido .titulo {
  text-align: center;
  font-family: "Dancing Script", cursive;
}
.header .contenido .titulo h1 {
  font-size: 3rem;
  text-transform: uppercase;
}
.header .contenido .titulo p {
  text-align: center;
  font-size: 2.5rem;
}
.header .contenido .descripcion h3 {
  text-align: center;
  font-family: "Dancing Script", cursive;
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  .header .contenido .descripcion h3 {
    font-size: 3rem;
  }
}
.header .flechas {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 3rem;
  color: #ffffff;
  animation: bajar infinite 2s;
}
@media (min-width: 480px) {
  .header .flechas {
    display: none;
  }
}

@keyframes bajar {
  0% {
    transform: translateY(1px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-11px);
  }
}
.menuMovil {
  visibility: visible !important;
  background-color: #051923;
  height: 80vh;
  z-index: 100;
  width: 100%;
}

.servicios {
  margin: 0;
  padding: 2rem;
  background: #051923;
}
.servicios .contenedorServicios {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .servicios .contenedorServicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.servicios .contenedorServicios .servicio {
  padding: 1rem;
  background-color: transparent;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 10px 15px 0px rgba(255, 255, 255, 0.5);
}
.servicios .contenedorServicios .servicio .icono {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background: #0582ca;
  color: #ffffff;
}
.servicios .contenedorServicios .servicio .titulo {
  text-align: center;
  color: #0582ca;
  font-weight: 700;
}
.servicios .contenedorServicios .servicio .descripcion {
  text-align: center;
  color: #ffffff;
}
.servicios .contenedorServicios .servicio .btnInfo {
  display: block;
  padding: 1rem;
  text-decoration: none;
  color: #051923;
  background-color: #00a6fb;
  border-radius: 1.5rem;
}
.servicios .contenedorServicios .servicio .btnInfo:hover {
  background-color: #003554;
  cursor: pointer;
  color: #fff;
}

.productos .titulo h2 {
  color: #051923;
}
.productos .titulo p {
  color: #051923;
}
.productos .contenedorProductos {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .productos .contenedorProductos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.productos .contenedorProductos .producto {
  position: relative;
  overflow: hidden;
}
.productos .contenedorProductos .producto .overlay {
  background: rgba(0, 52, 82, 0.589);
  width: 100%;
  color: #fff;
  position: absolute;
  bottom: -180px;
  transition: 1s ease-in-out;
}
.productos .contenedorProductos .producto .overlay .tituloOverlay {
  text-align: center;
  padding: 1rem;
}
.productos .contenedorProductos .producto:hover .overlay {
  bottom: 0px;
}

.contacto {
  margin: 0 auto;
  background-color: #051923;
}

.canales {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.canales .canal {
  padding: 2rem;
  color: #ffffff;
  text-align: center;
}
.canales .canal .icono {
  font-size: 3rem;
}

.footer {
  padding: 2rem;
  margin: 1px auto 0 auto;
  background: #051923;
  color: #ffffff;
  text-align: center;
  font-weight: 700;
}
.footer span {
  font-weight: normal;
}